function Projects (){

  const [isIntersecting, setIsIntersecting] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        setIsIntersecting(entry.isIntersecting);
      },
      { rootMargin: "100px" }
    );
    console.log(isIntersecting);
    observer.observe(ref.current);

    return () => observer.disconnect();
  }, [isIntersecting]);

  useEffect(() => {
    if (isIntersecting) {
      ref.current.querySelectorAll(".project").forEach((el) => {
        el.classList.add("square-animation");
      });
    } else {
      ref.current.querySelectorAll("div").forEach((el) => {
        el.classList.remove("square-animation");
      });
    }
  }, [isIntersecting]);

  

    return (
        <div className="App proj">    
        
          <div class="projects-container" ref={ref}>
            {projects.map((data, idx) => (
              <div class="project-wrapper" >
                <div class="project">
                  <h1>{data.name}</h1>
                  <p>{data.desc}</p>
                  <div class='badge-container'>   
                    {data.labels.map((lab, idxy) => (<Icon icon={lab} />))}
                  </div>
                </div>
              </div>
            ))}
          </div>
          {/* <ObserveAnimation /> */}
        </div>
      );
}






  
export default Projects;